import api from '@/common/index.js';
let mvc = {
	source: 0,
	addressList: []
}
export default {
	data() {
		return {
			mvc: {
				source: 0,
				addressList: []
			}
		}
	},

	onLoad(option) {
		let that = this;
		setTimeout(function() {
			api.pageTitle(that.$t('address').title)
			mvc.source = option.source;
			that.lodeData();
		}, 300)

	},
	computed: {
		i18n() {
			return this.$t("address")
		}
	},
	methods: {
		lodeData() {
			let that = this;
			that.$ajax.address().then(res => {
				res[0].default = true;
				mvc.addressList = res;
				that.mvc = mvc;
			})
		},
		
	}
}
